 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-08-16 13:18:49
 * @LastEditTime: 2019-08-20 15:18:30
 * @LastEditors: Please set LastEditors
 -->

<template>
  <div class="verify_wrapper">
    <Header></Header>

    <el-row type="flex" justify="center" align="middle" class="verify_content">
      <el-col :xs="4" :sm="6" :md="8" :lg="9" :xl="2" class="verify_page box">
        <div class="title">邮箱验证</div>
        <div class="deputy_title">请输入您的邮箱 {{email}} 收到的验证码</div>
        <div class="attention_title">邮箱验证码可能被判定为垃圾邮件，请注意查收</div>
        <el-form >
          <el-form-item prop="emailCode">
            <el-input v-model="emailCode" placeholder="邮箱验证码" autofocus="autofocus"  @keyup.enter.native="confirm"></el-input>
          </el-form-item>
          
          <el-form-item class="reset_btn">
            <el-button type="primary" @click.native="confirm">确认</el-button>
          </el-form-item>
        </el-form>

        <div class="tip">
          <div class="forget_password">没有收到验证码? <a href="javascript:;" @click="getCode">{{codes.text}}</a></div>
        </div>
      </el-col>
    </el-row>

    <user-foot></user-foot> 
  </div>
</template>

<script>
  import Header from '@/components/header/header'
  import UserFoot from '@/components/userFoot/userFoot'
  import { vaildEmail} from '@/utils/validate'

  export default {
    props: {},
    data() {
      return {
        email:'',
        emailCode:'',
        btnLoad:false,
        codes:{
          text:'获取验证码',
          status:false,
          time:60
        },
      }
    },
    created() {
      this.email = this.$route.query.email
    },
    methods: {
      /* 确认 */
      confirm:function(){
        if(!this.btnLoad){
          if(this.emailCode.length != 6){
            this.$message.info('请输入正确的验证码')
            return false
          }
          this.btnLoad = true
          const data = {"email":this.email , "capicha":this.emailCode}
          this.$store.dispatch('valid_email_status',data).then(res => {
            if(res.data.result == 0){
              this.btnLoad = false
              this.$router.replace({path:'/new',query:{email:this.email,code:this.emailCode}})
            }else{
              this.btnLoad = false
            }
          }).catch(err => {
            this.btnLoad = false
          })
        }
      },

      /* 获取验证码 */
      getCode(){
        if(!this.codes.status){

          if(!vaildEmail(this.email)){
            this.$message.info('请输入正确格式的邮箱')
            return false
          }
          var dataInfo = {"email":this.email}
          this.$store.dispatch('get_email_code',dataInfo).then(res => {
            if(res.data.result == 0){
              let _this = this;
              setTimeout(()=>{
                _this.codes.status = true
                _this.codes.text = _this.codes.time + 's 后重新发送'
              }, 800)
              
              var interval = window.setInterval(()=>{
                _this.codes.text = _this.codes.time + 's 后重新发送';
                --_this.codes.time
                if(_this.codes.time < 0){
                  _this.codes.text = '获取验证码'
                  _this.codes.time = 60
                  _this.codes.status = false
                  window.clearInterval(interval)
                }
              },1000)
            }
          }).catch(err => {

          })
        }
      },
    },
    components: {
      Header,UserFoot
    },
  };
</script>
<style lang="scss" scoped>
  @import '@/style/base.scss';

  .verify_wrapper{
    height: 100%;
    width: 100%;
    min-width: 1280px;
    padding-top: 47px;
    .verify_content{
      width: 100%;
      height: calc(100% - 180px);
      background-color:$module_bg_color;
      position: relative;
      .verify_page{
        width: 80%;
        max-width: 480px;
        min-width: 400px;
        max-height: 320px;
        border-radius: 5px;
        background-color: $light_font_color;
        padding:30px 50px;
        box-sizing: border-box;
        position: absolute;
        top:50%;
        left: 50%;
        margin-top: -180px;
        margin-left: -190px;
      }
      .title{
        width: 100%;
        height: 40px;
        line-height: 40px;
        font-size: $font20;
        letter-spacing: 2px;
        color: $light_title_color;
        text-align: center;
        margin-bottom: 20px;
      }
      .deputy_title{
        width: 100%;
        height: 20px;
        line-height: 20px;
        font-size: $base_font;
        color: $light_title_color;
        text-align: center;
        margin-bottom: 5px;
      }
      .attention_title{
        width: 100%;
        height: 20px;
        line-height: 20px;
        font-size: $base_font;
        color: $dark_attention_title_font_color;
        text-align: center;
        margin-bottom: 20px;
      }
      .reset_btn{
        margin-bottom: 15px;
        .el-button{
          width: 100%;
          background-color: $user_btn_bg_btn;
          border-radius: 3px;
          font-size: $font14;
          letter-spacing: 1px;
          margin-top: 10px;
        }
      }
      .tip{
        display: flex;
        div{
          width: 100%;
          height: 20px;
          line-height: 20px;
          color: $border_active_color;
        }
        .forget_password{
          text-align: center;
          font-size: $base_font;
          color:$dark_attention_title_font_color;
          a{
            color: $active_font_color;
            font-size: $base_font;
          }
        }
      }
    }
  }
</style>


